@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.cdnfonts.com/css/chicken-sans');
@import url('https://fonts.cdnfonts.com/css/poppins');

.bg-blue-color {
  @apply bg-[var(#032154b9)];
}

/* Target the scrollbar */
::-webkit-scrollbar {
  width: 10px; /* Width of the scrollbar */
}

/* Track - the area behind the scrollbar */
::-webkit-scrollbar-track {
  background: #ffffff; /* Color of the track */
}

/* Handle - the draggable part of the scrollbar */
::-webkit-scrollbar-thumb {
  background: #ffd000; /* Color of the handle */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; /* Color of the handle on hover */
}

body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Chicken Sans', sans-serif;
}
